import React from 'react'
import { Button } from "reactstrap";
import numberWithCommas from "reactnumberwithcommas";
import moment from 'moment-timezone'
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';




const Componen = (props) => {
    const dataDet = props.dataDet

    const closeModal = () => {
        props.setOpen(false)
    }

    return (
        <React.Fragment>
            <Dialog open={props.open} onClose={closeModal} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title">Detail</DialogTitle>
                <DialogContent>
                    <h6>Nomor Transaksi : <b>{dataDet.kodetrans}</b></h6>
                    <h6>Tanggal Bayar : <b>{moment(dataDet.tanggalBayar).format("DD-MM-YYYY HH:mm")}</b></h6>
                    <h6>Pasien : <b>{dataDet.pasien}</b></h6>
                    <h6>Dokter : <b>{dataDet.kodedokter}</b></h6>
                    <h6><b>Layanan</b> : <i>{numberWithCommas(dataDet.layanan.map(el => el.qty * el.harga).reduce((a, b) => a + b, 0))}</i></h6>
                    {dataDet.layanan.length > 0 && dataDet.layanan.map((el, i) => (
                        <p index={i}>- {el.namaLayanan} ({el.qty}) * {el.harga} {el.keterangan && (<small> ({el.keterangan}) </small>)}</p>
                    ))}
                    <h6><b>Obat Dari Dokter</b> : <i>{numberWithCommas(dataDet.resepObatdokter.map(el => el.qty * el.harga).reduce((a, b) => a + b, 0))}</i></h6>
                    {dataDet.resepObatdokter.length > 0 && dataDet.resepObatdokter.map((el, i) => (
                        <p index={i}>- {el.namaProduk}  ({el.qty}) * {el.harga} {el.keterangan && (<small> ({el.keterangan}) </small>)}</p>
                    ))}
                    <h6><b>Peralatan</b> : <i>{numberWithCommas(dataDet.peralatan.map(el => el.qty * el.harga).reduce((a, b) => a + b, 0))}</i></h6>
                    {dataDet.peralatan.length > 0 && dataDet.peralatan.map((el, i) => (
                        <p index={i}>- {el.namaProduk} ({el.qty}) * {el.harga}</p>
                    ))}
                    <h6><b>Total Tagihan</b> : <b><i>{numberWithCommas(dataDet.totalTagihan)}</i></b></h6>
                    <h6><b>Total Bayar</b> : <b><i>{numberWithCommas(dataDet.jumlahBayar)}</i></b></h6>
                    <h6><b>Kembalian</b> : <b><i>{numberWithCommas(dataDet.kembalian)}</i></b></h6>
                </DialogContent>
                <DialogActions>
                    <Button onClick={closeModal} color="secondary">Close</Button>
                </DialogActions>
            </Dialog>
        </React.Fragment>
    )
}

export default Componen;

