import GQL from '../helpers/GQL'


const Laporan = {
    laporanPelayanan: (clientID, token, kodeklinik, tglDari, tglSampai) => {
        return new Promise((resolve, reject) => {
            const query = `
                query {
                    laporanPelayananPasien(kodeklinik:"${kodeklinik}", tglDari:"${tglDari}", tglSampai:"${tglSampai}"){
                        _id
                    kodetrans
                    kodedokter
                    pasien
                    totalTagihan
                    tanggalBayar
                    jumlahBayar
                    kembalian
                    statusID
                    alasanBatal
                    janji{
                        tanggalJanji
                        jamMulaiJanji
                        jamSelesaiJanji
                    }
                    keperluanPasien
                    layanan{
                        kodeLayanan
                        namaLayanan
                        harga
                        qty
                        keterangan
                    }
                    peralatan{
                        kodeProduk
                        namaProduk
                        harga
                        qty
                        satuan
                    }
                    resepObatdokter{
                        kodeProduk
                        namaProduk
                        harga
                        qty
                        satuan
                        keterangan
                        }
                    }
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                const laporan = []
                const lapBatal = []
                resp.data.laporanPelayananPasien.map(el => {
                    if (el.statusID === 5) {
                        return laporan.push(el)
                    } else {
                        return lapBatal.push(el)
                    }
                })

                if (resp.data && resp.data.laporanPelayananPasien) return (resolve({ laporan, lapBatal }));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
};

export default Laporan;
