import React, { Fragment, useEffect, useState } from "react"
import { Row, Button, Col, Card, CardBody, Breadcrumb, BreadcrumbItem } from "reactstrap"
import { Colxx, Separator } from "../../../../components/common/CustomBootstrap"
import { ContextMenuTrigger } from "react-contextmenu"
import mUser from '../../../../Controler/c_User'
import mLaporan from '../../../../Controler/c_Laporan'
import IntlMessages from "../../../../helpers/IntlMessages"
import LoadingRow from "../../../../helpers/LoadingRow"
import DataTablePagination from "../../../../components/DatatablePagination"
import ReactTable from "react-table"
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import moment from 'moment-timezone'
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import numberWithCommas from "reactnumberwithcommas";
import MenuItem from '@material-ui/core/MenuItem';
import PelayananDet from './PelayananDet'

const Admin = (props) => {
    const authLogin = JSON.parse(localStorage.getItem('keminaAdmin'))
    const [isReload, setIsReload] = useState(true)
    const [user, setUser] = useState({})
    const [tgldari, setTgldari] = useState(moment())
    const [tglSampai, setTglSampai] = useState(moment())
    const [isOpenDet, setIsOpenDet] = useState(false)
    const [loadHakAkses, setLoadHakAkses] = useState(true)
    const [listLaporan, setlistLaporan] = useState([])
    const [lisLapBatal, setLisLapBatal] = useState([])
    const [dataDet, setDataDet] = useState({})
    const [status, setStatus] = useState(5)

    useEffect(() => {
        setlistLaporan([]);
        setLisLapBatal([]);
        mLaporan.laporanPelayanan(authLogin.clientID, authLogin.token, authLogin.klinik.kodeklinik, moment(tgldari).format("YYYY-MM-DD"), moment(tglSampai).format("YYYY-MM-DD")).then(resp => {
            setlistLaporan(resp.laporan);
            setLisLapBatal(resp.lapBatal);
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsReload(false));
        return () => { };
    }, [authLogin.clientID, authLogin.token, authLogin.username, authLogin.klinik.kodeklinik, isReload, tgldari, tglSampai]);
    useEffect(() => {
        mUser.dataUser(authLogin.clientID, authLogin.token, authLogin.username, authLogin.jenisUser).then(resp => {
            setUser(resp);
        }).catch(err => {
            console.error(err);
        }).finally(() => setLoadHakAkses(false))
        return () => {

        }
    }, [authLogin.clientID, authLogin.token, authLogin.username, authLogin.jenisUser, loadHakAkses])

    const handleDet = (data) => {
        setDataDet(data)
        setIsOpenDet(true)

    }
    let dataTableColumns = []
    if (status === 5) {
        dataTableColumns = [
            {
                Header: "No Trans",
                accessor: "kodetrans",
                Cell: props => <p className="text-left"> {props.value}</p>
            },
            {
                Header: "Tanggal",
                accessor: "tanggalBayar",
                Cell: props => <p className="text-left"> {moment(props.value).format("DD-MM-YYYY")}</p>
            },
            {
                Header: "Pasien",
                accessor: "pasien",
                Cell: props => <p className="text-left"> {props.value}</p>
            },
            {
                Header: "Dokter",
                accessor: "kodedokter",
                Cell: props => <p className="text-left"> {props.value}</p>
            },
            {
                Header: "Total Biaya",
                accessor: "totalTagihan",
                Cell: props => <p className="text-left"> {numberWithCommas(props.value)}</p>
            },
            {
                Header: <p className="text-left">Aksi</p>,
                Cell: props => <p className="text-muted">
                    <Button style={{ marginRight: 3 }} color="warning" onClick={handleDet.bind(this, props.original)}>Detail</Button>
                </p>
            }
        ];
    } else {
        dataTableColumns = [
            {
                Header: "Tanggal",
                accessor: "tanggalJanji",
                Cell: props => <p className="text-left"> {moment(props.value).format("DD-MM-YYYY")}</p>
            },
            {
                Header: "Pasien",
                accessor: "pasien",
                Cell: props => <p className="text-left"> {props.value}</p>
            },
            {
                Header: "Alasan Batal",
                accessor: "alasanBatal",
                Cell: props => <p className="text-left"> {props.value}</p>
            },
        ];
    }
    if (loadHakAkses) {
        return <LoadingRow />
    } else {
        if (user.jenisUser === "admin") {
            return (
                <Fragment>
                    <Row>
                        <Colxx xxs="12">
                            <div className="mb-2">
                                <h1>
                                    Laporan
                                </h1>
                                <Breadcrumb className="pt-0 breadcrumb-container d-none d-sm-block d-lg-inline-block">
                                    <BreadcrumbItem>
                                        <IntlMessages id="menu.laporan-pelayanan" />
                                    </BreadcrumbItem>
                                </Breadcrumb>
                            </div>

                            <Separator className="mb-5" />
                        </Colxx>
                    </Row>
                    <Row>
                        <Col xs={4}>
                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                <DatePicker
                                    fullWidth={true}
                                    label="Tanggal Dari"
                                    format="dd-MM-yyyy"
                                    value={tgldari}
                                    onChange={(d) => setTgldari(d)}
                                    ampm={false}
                                />
                            </MuiPickersUtilsProvider>
                        </Col>
                        <Col xs={4}>
                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                <DatePicker
                                    fullWidth={true}
                                    label="Tanggal Sampai"
                                    format="dd-MM-yyyy"
                                    value={tglSampai}
                                    onChange={(d) => setTglSampai(d)}
                                    ampm={false}
                                />
                            </MuiPickersUtilsProvider>
                        </Col>
                        <Col xs={4}>
                            <FormControl fullWidth>
                                <InputLabel id="kategori">Status</InputLabel>
                                <Select
                                    labelId="kategori"
                                    id="kategori"
                                    name="kategori"
                                    value={status}
                                    onChange={(e) => setStatus(e.target.value)}
                                >
                                    <MenuItem value={5}>Berobat</MenuItem>
                                    <MenuItem value={0}>Batal</MenuItem>

                                </Select>
                            </FormControl>
                        </Col>
                        <Colxx xxs="12" className="mb-4 jarakAtas">
                            <ContextMenuTrigger id="menu_id" >
                                <Card>
                                    <CardBody>
                                        <ReactTable
                                            data={status === 5 ? listLaporan : lisLapBatal}
                                            columns={dataTableColumns}
                                            defaultPageSize={5}
                                            filterable={false}
                                            showPageJump={true}
                                            PaginationComponent={DataTablePagination}
                                            showPageSizeOptions={true}
                                        />
                                    </CardBody>
                                </Card>
                            </ContextMenuTrigger>
                        </Colxx>
                    </Row>
                    {isOpenDet && <PelayananDet open={isOpenDet} setOpen={setIsOpenDet} dataDet={dataDet} />}
                </Fragment>
            )
        } else {
            return (
                <p>You do not have access rights</p>
            )
        }
    }
}
export default Admin;