import React from 'react'
import Skeleton from 'react-loading-skeleton';
import { Table } from 'reactstrap';




const LoadingTabel = () => {
    const loadingRow = [0, 1, 2, 3, 4];
    return (
        <React.Fragment>
            <Table>
                <tbody>
                    {loadingRow.map((el) => (
                        <tr key={el.toString()}>
                            <th><Skeleton /></th>
                            <td><Skeleton /></td>
                            <td><Skeleton /></td>
                        </tr>
                    ))}
                </tbody>
            </Table>
        </React.Fragment>
    )
}

export default LoadingTabel;
